.Routines {
  padding: 1em;
  font-family: 'Libre Baskerville', serif;
}

.Routines h2 {
  margin-bottom: .5em;
  padding-bottom: .5em;
  border-bottom: 1px solid grey;
}

.activities {
  margin-bottom: 1em;
  max-width: 40em;
  
}
@media all and (min-width: 480px) {
    .CreateRoutine {
      position: fixed;
      background: #00000050;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
    }
  
    .CreateRoutine form {
      position: relative;
      width: 80%;
      margin: 0 auto;
      height: auto;
      max-height: 80vh;
      margin-top: calc(100vh - 85vh - 20px);
      background: #fff;
      border-radius: 4px;
      padding: 20px;
      border: 1px solid #999;
      overflow: auto;
    }
  }
  
  .close-icon {
      content: 'x';
      cursor: pointer;
      position: fixed;
      right: calc(15% - 30px);
      top: calc(100vh - 85vh - 33px);
      background: #ededed;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      line-height: 20px;
      text-align: center;
      border: 1px solid #999;
      font-size: 20px;
  }
